.editContractModal {
  padding: 20px 20px 40px 20px;
}
.editContract {
  &__form-row {
    display: flex;
    flex-direction: row;
    margin-bottom: 10px;
    & > span {
      flex-basis: 50%;
    }
    & > input {
      flex-basis: 100%;
    }
    & .editContract__form-column {
      display: flex;
      flex-basis: 50%;
    }
  }
  &__form-column {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    &:first-of-type {
      margin-right: 10px;
    }
    &:last-of-type {
      margin-left: 10px;
    }
  }
}
</style>
